export default {
  data() {
    return {
      searchKey: "",
      pager: {
        pager: {
          pageCount: 0,
          pageNumber: 1,
          pageSize: 15,
          recordCount: 0
        }
      },
      selected: [],
      options: [],
      addEditShow: false,
      grantShow: false,
      seller: {
        id: 0,
        name: "",
        description: "",
        avatar: "",
        url:"",
        saleTime:"",
        address:"",
        telephone:"",
        province:"北京市"
      },
      formLabelWidth: "120px"
    };
  },
  computed: {
    dialogWidth() {
      return 890 * 100 / this.$utils.windowWidth() + "%";
    }
  },
  watch: {
    options: function() {
      this.selected = [];
      this.options.forEach(item => {
        if (item.selected) {
          this.selected.push(item.key);
        }
      });
    }
  },
  methods: {
    addSeller() {
      this.seller = {
        id: 0,
        url: '',
        province:'北京市'
      };
      this.addEditShow = true;
    },
    target_href(){

    },
    changePage() {
      if (this.searchKey) {
        this.doSearch();
      } else {
        this.loadData();
      }
    },
    doSearch() {
      this.$api.Seller.search(
        this.pager.pager.pageNumber,
        this.searchKey,
        result => {
          this.pager = result.pager;
        }
      );
    },
    saveSeller(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          var callback = result => {
            this.changePage();
            this.addEditShow = false;
          };
          console.log(this.seller)
          this.$api.Seller.save(this.seller, callback);
        } else {
          return false;
        }
      });
    },
    handleEdit(index, row) {
      this.seller = row;
      this.addEditShow = true;
    },
    handleDelete(index, row) {
      this.$confirm(row.deleted?"确认恢复?":"确认删除?", row.deleted?"恢复确认":"删除确认", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$api.Seller.status(row.id, result => {
          this.$message({
            type: "success",
            message: row.deleted?"恢复成功!":"删除成功!"
          });
          window.setTimeout(() => {
            this.changePage();
          }, 2000);
        });
      });
    },
    loadData() {
      this.$api.Seller.list(this.pager.pager.pageNumber, result => {
        this.pager = result.pager;
      });
    }
  },
  created: function() {
    this.loadData();
  }
};



