import BScroll from 'better-scroll'
import Split from '../split/Split'
import CartControl from '../cartcontrol/CartControl'
import Vue from "vue"
import {mapActions,mapState} from "vuex"

export default {
  data() {
    return {
      showFlag: false
    }
  },
  props: {
    food: {
      type: Object
    }
  },
  methods: {
    ...mapActions(["updateMealOrder"]),
    addProduct() {
      if(!this.orderedMeal){
        if (!this.food.count) {
          Vue.set(this.food, "count", 1)
          this.updateMealOrder(this.food);
        }
      }
    },
    showView() {
      this.showFlag = true
      this.$nextTick(() => {
        if (!this.scroll) {
          this.scroll = new BScroll(this.$refs.foodView, {
            click: true
          })
        } else {
          this.scroll.refresh()
        }
      })
    },
    closeView() {
      this.showFlag = false
    }
  },
  components: {
    CartControl: CartControl,
    Split: Split
  },
  computed: {
    ...mapState({
      orderedMeal: state => state.orderedMeal
    })
  }
}



