export default {
  data() {
    return {
      searchKey: "",
      pager: {
        pager: {
          pageCount: 0,
          pageNumber: 1,
          pageSize: 15,
          recordCount: 0
        }
      },
      selected: [],
      options: [],
      addEditShow: false,
      grantShow: false,
      role: {
        id: 0,
        name: "",
        description: "",
        installed: false
      },
      formLabelWidth: "120px"
    };
  },
  computed: {
    dialogWidth() {
      return 590 * 100 / this.$utils.windowWidth() + "%";
    }
  },
  watch: {
    options: function() {
      this.selected = [];
      this.options.forEach(item => {
        if (item.selected) {
          this.selected.push(item.key);
        }
      });
    }
  },
  methods: {
    addRole() {
      this.role = {
        id: 0,
        name: "",
        description: "",
        installed: false
      };
      this.addEditShow = true;
    },
    grant() {
      this.$api.Role.grant(this.role.id, this.selected, result => {
        this.$message({
          type: "success",
          message: "授权成功!"
        });
        window.setTimeout(() => {
          this.grantShow = false;
        }, 2000);
      });
    },
    changePage() {
      if (this.searchKey) {
        this.doSearch();
      } else {
        this.loadData();
      }
    },
    doSearch() {
      this.$api.Role.search(
        this.pager.pager.pageNumber,
        this.searchKey,
        result => {
          this.pager = result.pager;
        }
      );
    },
    saveOrUpdateRole(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          var callback = result => {
            this.changePage();
            this.addEditShow = false;
          };
          this.role.id
            ? this.$api.Role.update(this.role, callback)
            : this.$api.Role.save(this.role, callback);
        } else {
          return false;
        }
      });
    },
    handleEdit(index, row) {
      this.role = row;
      this.addEditShow = true;
    },
    handleGrant(index, row) {
      this.role.id = row.id;
      this.$api.Role.roleGrantInfo(row.id, result => {
        this.options = [];
        result.infos.forEach((item, index) => {
          this.options.push({
            key: item.id,
            label: item.description,
            selected: item.selected
          });
        });
        this.grantShow = true;
      });
    },
    handleDelete(index, row) {
      this.$confirm("确认删除角色?", "删除确认", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$api.Role.delete(row.id, result => {
          this.$message({
            type: "success",
            message: "删除成功!"
          });
          window.setTimeout(() => {
            this.changePage();
          }, 2000);
        });
      });
    },
    loadData() {
      this.$api.Role.list(this.pager.pager.pageNumber, result => {
        this.pager = result.pager;
      });
    }
  },
  created: function() {
    this.loadData();
  }
};



