export default {
  data() {
    return {
      searchKey: "",
      pager: {
        pager: {
          pageCount: 0,
          pageNumber: 1,
          pageSize: 15,
          recordCount: 0
        }
      },
      selected: [],
      options: [],
      addEditShow: false,
      grantShow: false,
      mcategory: {
        id: 0,
        name: "",
        description: "",
        avatar: "",
        url:"",
        saleTime:"",
        address:"",
        telephone:""
      },
      formLabelWidth: "120px"
    };
  },
  computed: {
    dialogWidth() {
      return 590 * 100 / this.$utils.windowWidth() + "%";
    }
  },
  watch: {
    options: function() {
      this.selected = [];
      this.options.forEach(item => {
        if (item.selected) {
          this.selected.push(item.key);
        }
      });
    }
  },
  methods: {
    addMCategory() {
      this.mcategory = {
        id: 0,
        url: ""
      };
      this.addEditShow = true;
    },
    target_href(){

    },
    changePage() {
      if (this.searchKey) {
        this.doSearch();
      } else {
        this.loadData();
      }
    },
    doSearch() {
      this.$api.MCategory.search(
        this.pager.pager.pageNumber,
        this.searchKey,
        result => {
          this.pager = result.pager;
        }
      );
    },
    saveMCategory(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          var callback = result => {
            this.changePage();
            this.addEditShow = false;
          };
          this.$api.MCategory.save(this.mcategory, callback);
        } else {
          return false;
        }
      });
    },
    handleEdit(index, row) {
      this.mcategory = row;
      this.addEditShow = true;
    },
    handleDelete(index, row) {
      this.$confirm("确认删除?", "删除确认", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$api.MCategory.status(row.id, result => {
          this.$message({
            type: "success",
            message: "删除成功!"
          });
          window.setTimeout(() => {
            this.changePage();
          }, 2000);
        });
      });
    },
    loadData() {
      this.$api.MCategory.list(this.pager.pager.pageNumber, 0,result => {
        this.pager = result.pager;
      });
    }
  },
  created: function() {
    this.loadData();
  }
};



