export default {
  data() {
    return {
      searchKey: "",
      pager: {
        pager: {
          pageCount: 0,
          pageNumber: 1,
          pageSize: 15,
          recordCount: 0
        }
      },
      selected: [],
      addEditShow: false,
      Permission: {
        id: 0,
        name: "",
        description: "",
        installed: false
      },
      formLabelWidth: "120px"
    };
  },
  methods: {
    addPermission() {
      this.Permission = {
        id: 0,
        name: "",
        description: "",
        installed: false
      };
      this.addEditShow = true;
    },
    changePage() {
      if (this.searchKey) {
        this.doSearch();
      } else {
        this.loadData();
      }
    },
    doSearch() {
      this.$api.Permission.search(this.pager.pager.pageNumber, this.searchKey, result => {
        this.pager = result.pager;
      });
    },
    saveOrUpdatePermission(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          var callback = result => {
            this.changePage();
            this.addEditShow = false;
          };
          this.Permission.id
            ? this.$api.Permission.update(this.Permission, callback)
            : this.$api.Permission.save(this.Permission, callback);
        } else {
          return false;
        }
      });
    },
    handleEdit(index, row) {
      this.Permission = row;
      this.addEditShow = true;
    },
    handleDelete(index, row) {
      this.$confirm("确认删除权限?", "删除确认", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$api.Permission.delete(row.id, result => {
          this.$message({
            type: "success",
            message: "删除成功!"
          });
          window.setTimeout(() => {
            this.changePage();
          }, 2000);
        });
      });
    },
    loadData() {
      this.$api.Permission.list(this.pager.pager.pageNumber, result => {
        this.pager = result.pager;
      });
    }
  },
  created: function() {
    this.loadData();
  }
};



