
import BScroll from 'better-scroll'
import AlertModule from 'vux/src/plugins/alert/module'

import CartControl from '../cartcontrol/CartControl'
import { mapState,mapActions } from "vuex";
export default {
data() {
return {
fold: true,
alertShow: false,
meal:false
}
},
props: {
selectFoods: {
type: Array,
default() {
return []
}
}
},
components: {

},
computed: {
...mapState({
orderedMeal: state => state.orderedMeal,
orderMeals: state => state.orderMeals
}),
totalCount() {
var count = 0
this.orderMeals.forEach(food => {
count += food.count
})
return count
},
cart_list: function () {
return this.orderMeals.filter(function (food) {
return food
})
},
totalPrice() {
let total = 0;
this.orderMeals.forEach((food) => {
total += food.count * food.price
})
return total;
},
payStr() {
let tCount = this.totalPrice;
if (this.totalCount > 0) {
if(tCount > 20){
return "不可超过¥20"
}
if(this.meal){
return "取消订餐"
}
return "去结算"
} else {
return ""
}
},
listShow() {
if (!this.totalCount) {
this.fold = true;
return false
}
let show = !this.fold;
if (show) {
this.$nextTick(() => {
if (!this.shopScroll) {
this.shopScroll = new BScroll(this.$refs.listContent, {
click: true
})
} else {
this.shopScroll.refresh()
}
}
)
}
return show
}
},
methods: {
...mapActions(["commitMeal","cancelMeal","clearMealOrders"]),
toggleList() {
if (!this.totalCount) {
return;
}
this.fold = !this.fold
},
initMeal(){
this.meal = this.orderedMeal;
},
pay() {
if(this.meal){
this.$api.DingCan.cancel_order(result => {
this.clearAll();
this.meal = false;
this.alert('订餐取消','取消成功');
});
}else{
if (this.totalPrice > 0 &&  this.totalPrice <= 20) {
this.$api.DingCan.submit_order(this.cart_list, result => {
this.commitMeal();
this.meal =  true;
this.alert('订餐','订餐成功');
});
}
}
},
alert(title,content){
AlertModule.show({title: title,content: content,
onShow () {
console.log('Module: I\'m showing')
},
onHide () {
console.log('Module: I\'m hiding now')
}
})
},
clearAll() {
this.selectFoods.forEach((food) => {
food.count = 0;
})
this.cancelMeal();
this.clearMealOrders();
this.orderMeals.forEach((order)=>{
console.log('clearAll ',order);
});
},
hideMask() {
this.fold = true;
}
},
components: {
"app-cart-control": CartControl
},
mounted () {
this.initMeal();
}
}



