  import Header from "@/components/header/Header"
  import Nav from "@/components/nav/Nav"
  import CartControl from '@/components/cartcontrol/CartControl'
  import BScroll from 'better-scroll'
  import Shopcart from '@/components/shopcart/Shopcart'
  import ProductDetail from '@/components/productDetail/ProductDetail'
  import { mapState,mapActions } from "vuex";
  export default {
    data() {
      return {
        commentNum: 0,
        container: {},
        goods: [],
        listHeight: [],
        menuScroll: {},
        foodScroll: {},
        scrollY: 0,
        selectFood: {}
      }
    },
    components: {
      "app-header": Header,
      "app-nav": Nav,
      "app-shopcart": Shopcart,
      "app-cart-control": CartControl,
      "app-product-detail": ProductDetail
    },
    created() {
      this.init_data(false);
    },
    methods: {
      head_bg(imgName) {
        return "background-image: url(" + imgName + ");"
      },
      init_data(reload){
        let id = this.$route.params.id;
        if(!id){
          id = 2;
        }
        this.$api.DingCan.new_goods(id,result => {
          this.container = result.data.container_operation_source
          this.goods = result.data.food_spu_tags
          if(!reload){
          //DOM已经更新
            this.$nextTick(() => {
              // 执行滚动方法
              this.initScroll()
              // 计算分类的区间高度
              this.calculateHeight()
            })
          }
        });
      },
      initScroll() {
        this.menuScroll = new BScroll(this.$refs.menuScroll, {
          click: true
        })
        this.foodScroll = new BScroll(this.$refs.foodScroll, {
          probeType: 3,
          click: true
        })
        //foodScroll监听事件
        this.foodScroll.on("scroll", (pos) => {
          this.scrollY = Math.abs(pos.y)
        })

      },
      calculateHeight() {
        let foodlist = this.$refs.foodScroll.getElementsByClassName("food-list-hook")
        let height = 0
        this.listHeight.push(height)
        for (let i = 0; i < foodlist.length; i++) {
          let item = foodlist[i]
          // 累加
          height += item.clientHeight
          this.listHeight.push(height)
        }
      },
      selectMenu(index) {
        let foodlist = this.$refs.foodScroll.getElementsByClassName("food-list-hook")
        let element = foodlist[index]
        this.foodScroll.scrollToElement(element, 250)
      },
      calculateCount(spus) {
        let count = 0
        spus.forEach((food) => {
          if (food.count > 0) {
            count += food.count
          }
        })
        return count
      },
      showDetail(food) {
        this.selectFood = food;
        this.$refs.foodView.showView()
      }
    },
    watch: {
    $route(to, from) {
      if(to.name=='菜单'){
        this.init_data(true);
      }
		}
  },
    computed: {
      ...mapState({
        orderedMeal: state => state.orderedMeal,
        orderMeals: state => state.orderMeals
      }),
      currentIndex() {
        for (let i = 0; i < this.listHeight.length; i++) {
          let height1 = this.listHeight[i];
          let height2 = this.listHeight[i + 1];
          if (!height2 || (this.scrollY >= height1 && this.scrollY < height2)) {
            return i;
          }
        }
        return 0
      },
      selectFoods() {
        let foods = []
        this.goods.forEach((item) => {
          item.spus.forEach((food) => {
            this.orderMeals.forEach(item2 => {
              if(item2.id==food.id){
                  food.count = item2.count;
                  return
              }
            });
            if (food.count > 0) {
              foods.push(food)
            }
          })
        })
        return foods
      }
    }
  }



