import moment from "moment";

export default {
  data() {
    return {
      searchKey: "",
      pager: {
        pager: {
          pageCount: 0,
          pageNumber: 1,
          pageSize: 15,
          recordCount: 0
        }
      },
      selected: [],
      options: [],
      addEditShow: false,
      resetShow: false,
      grantShow: false,
      type: "role",
      user: {
        id: 0,
        name: "",
        realName: "",
        status: "ACTIVED",
        password: "",
        rePassword: "",
      },
      formLabelWidth: "100px"
    };
  },
  computed: {
    dialogWidth() {
      return 590 * 100 / this.$utils.windowWidth() + "%";
    }
  },
  watch: {
    options: function() {
      this.selected = [];
      this.options.forEach(item => {
        if (item.selected) {
          this.selected.push(item.key);
        }
      });
    }
  },
  methods: {
    grant() {
      let url = "/user/grant/" + this.type;
      let data = {
        userId: this.user.id,
        grantIds: this.selected
      };
      this.$api.User.grant(this.user.id, this.type, this.selected, result => {
        this.$message({
          type: "success",
          message: "授权成功!"
        });
        window.setTimeout(() => {
          this.grantShow = false;
        }, 2000);
      });
    },
    resetPassword(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          console.log(this.user.id);
          this.$api.User.rebuilt(
            this.user.id,
            this.user.password,
            result => {
              this.$message({
                type: "success",
                message: "重置成功!"
              });
              this.resetShow = false;
            }
          );
        } else {
          return false;
        }
      });
    },
    handleReset(index, row) {
      this.user.id = row.id;
      this.user.name = row.name;
      this.resetShow = true;
    },
    changePage() {
      if (this.searchKey) {
        this.doSearch();
      } else {
        this.loadData();
      }
    },
    doSearch() {
      this.$api.User.search(
        this.pager.pager.pageNumber,
        this.searchKey,
        result => {
          this.pager = result.pager;
        }
      );
    },
    checkSame() {
      return this.user.password === this.user.rePassword;
    },
    addUser() {
      this.addEditShow = true;
      this.user = {
        id: 0,
        name: "",
        realName: "",
        status: "ACTIVED",
        password: "",
        rePassword: "",
        phone: "",
        email: ""
      };
    },
    saveOrUpdateUser(formName) {
      this.$refs[formName].validate(valid => {
        if (valid && this.checkSame()) {
          var callback = result => {
            this.changePage();
            this.addEditShow = false;
          };
          this.user.id
            ? this.$api.User.update(this.user, callback)
            : this.$api.User.save(this.user, callback);
        } else {
          return false;
        }
      });
    },
    formatter(row, column) {
      return moment(row.createDate, "YYYY-MM-DD hh:mm:ss").format(
        "YYYY年MM月DD日"
      );
    },
    handleEdit(index, row) {
      let id = this.pager.dataList[index].id;
      this.user = row;
      this.user.password = "00000000";
      this.user.rePassword = "00000000";
      this.addEditShow = true;
    },
    handleGrant(index, row, type) {
      this.user.id = row.id;
      this.type = type;
      this.$api.User.userGrantInfo(type, row.id, result => {
        this.options = [];
        result.infos.forEach((item, index) => {
          this.options.push({
            key: item.id,
            label: item.description,
            selected: item.selected
          });
        });
        this.grantShow = true;
      });
    },
    handleDelete(index, row) {
      this.$confirm("确认删除用户?", "删除确认", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      }).then(() => {
        this.$api.User.delete(row.id, result => {
          this.$message({
            type: "success",
            message: "删除成功!"
          });
          window.setTimeout(() => {
            if (this.searchKey) {
              this.doSearch();
            } else {
              this.loadData();
            }
          }, 2000);
        });
      });
    },
    loadData() {
      this.$api.User.list(this.pager.pager.pageNumber, result => {
        this.pager = result.pager;
      });
    }
  },
  mounted: function() {
    this.loadData();
  }
};



